#services {
  padding: 4rem 0;
  background-color: #f9f9f9;
}

.services-slider {
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 40px; /* Espaço para os pontinhos */
}

.service-card {
  background-color: #fff;
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; /* Centraliza o conteúdo horizontalmente */
  width: 100%;
  height: 300px; /* Altura fixa para manter o tamanho igual */
  max-width: 250px; /* Largura fixa para manter a proporção */
}

.service-card img {
  max-width: 100px; /* Ajuste o tamanho da imagem para ser proporcional */
  margin-bottom: 1rem;
  border-radius: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto; /* Garante centralização da imagem */
}

.service-card h3 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.service-card p {
  font-size: 1rem;
  color: #555;
  line-height: 1.5;
  max-height: 4.5em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Limitar a 3 linhas */
  -webkit-box-orient: vertical;
}

/* Estilo para as setas do carrossel */
.slick-prev, .slick-next {
  background-color: #ffffff; /* Remove o fundo */
  border: none; /* Remove a borda */
  width: auto; 
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  color: #ffffff; /* Cor consistente para as setas */
}

.slick-prev:before, .slick-next:before {
  font-size: 60px; /* Ajuste o tamanho das setas */
  color: #0078D4; /* Mesma cor para as setas */
}

.slick-prev {
  left: 10px; /* Ajuste a posição horizontal */
}

.slick-next {
  right: 60px; /* Ajuste a posição horizontal */
}

.slick-dots {
  display: block !important; /* Forçar a exibição */
  text-align: center;
  bottom: 10px;
}

.slick-dots li button:before {
  color: #000000; /* A cor dos pontinhos */
}


.slick-dots li {
  display: inline-block;
  margin: 0px;
}

.slick-dots li button:before {
  font-size: 5px;
  color: #333;
}


.slick-slide {
  padding: 0 10px; /* Define um espaço entre os slides */
}

