#email {
    padding: 4rem 0;
    background-color: #f9f9f9;
    text-align: center;
  }
  
  form {
    max-width: 600px;
    margin: 0 auto;
  }
  
  input, textarea {
    width: 100%;
    padding: 1rem;
    margin: 1rem 0;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  button {
    padding: 1rem 2rem;
    background-color: #0b0c3f;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  button:hover {
    background-color: #333;
  }
  /* Adicione isso ao seu arquivo Email.css */

/* Adicione isso ao seu arquivo Email.css */

.required {
    color: red;
    font-size: 1rem;
  }
  
  label {
    display: block;
    margin-bottom: 1rem;
  }
  
  input, textarea {
    display: block;
    width: 100%;
    padding: 0.5rem;
    margin-top: 0.25rem;
  }
  
  button {
    padding: 0.5rem 1rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .error {
    color: red;
    font-size: 0.875rem;
    margin-top: 0.5rem;
  }
  
  